.player-container {
  max-width: 700px;
  margin: auto;
  padding: 2rem;
}

.player-container .player {
  background: #000;
  width: 100%;
  height: 0;
  padding-bottom: 56.25%;
  position: relative;
  overflow: hidden;
  box-shadow: 0 10px 0 -3px rgba(0, 0, 0, .2);
}

.player-container .player:fullscreen {
  padding-bottom: 100vh;
}

.player-container .player:-webkit-full-screen {
  padding-bottom: 100vh;
}

.player-container .player:-moz-full-screen {
  padding-bottom: 100vh;
}

.player-container .player:-ms-fullscreen {
  padding-bottom: 100vh;
}

.player-container .player:hover .controls {
  transition: all .2s ease-out;
  bottom: 0;
}

.player-container .player video {
  width: 100%;
  height: auto;
}

.player-container .player .controls {
  box-sizing: border-box;
  background: linear-gradient(rgba(37, 37, 37, 0) 10%, rgba(37, 37, 37, .6) 80%);
  width: 100%;
  height: 48px;
  padding: 0;
  transition: all .2s ease-in 5s;
  position: absolute;
  bottom: -80px;
}

.player-container .player .controls .time {
  padding-bottom: 14px;
  position: absolute;
  bottom: 100%;
  right: 30px;
}

.player-container .player .controls .progress {
  cursor: pointer;
  background: rgba(60, 60, 60, .6);
  border-radius: 6px;
  width: calc(100% - 40px);
  height: 8px;
  margin: auto;
  transition: height .1s ease-in-out;
  position: absolute;
  bottom: 100%;
  left: 20px;
}

.player-container .player .controls .progress:hover {
  height: 10px;
}

.player-container .player .controls .progress .progress-filled {
  background: #0094ff;
  border-radius: 6px;
  width: 0;
  height: 100%;
  transition: all .1s;
}

.player-container .player .controls .controls-main {
  width: calc(100% - 2rem);
  margin: auto;
}

.player-container .player .controls .controls-main .material-icons {
  margin-top: .25rem;
  font-size: 2.5rem;
}

.player-container .player .controls .controls-main .speed-list {
  text-align: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
}

.player-container .player .controls .controls-main .speed-list li {
  color: #fff;
  cursor: pointer;
  padding: 5px;
  font-size: 1.5rem;
}

.player-container .player .controls .controls-main .speed-list li:hover, .player-container .player .controls .controls-main .speed-list li.active {
  color: #0094ff;
}

.player-container .player .controls .controls-main .volume-slider {
  cursor: pointer;
  background: rgba(60, 60, 60, .6);
  border-radius: 6px;
  width: 80px;
  height: 8px;
  position: relative;
}

.player-container .player .controls .controls-main .volume-slider .volume-filled {
  background: #fff;
  border-radius: 6px;
  width: 100%;
  height: 100%;
  transition: width .2s ease-in-out;
}

.player-container .player .controls .controls-main .volume-slider .volume-filled:hover {
  background: #0094ff;
}

.thumb {
  text-align: center;
  text-transform: uppercase;
  cursor: pointer;
  background-size: 100%;
  border: 1px solid #919191;
  width: 23%;
  font-size: 80%;
  line-height: 50px;
}

